<template>
<div class="classification">
  <ts-section>
    <div slot="menu">
      <ts-button type="primary" @click="Classification.newDialog=true"></ts-button>
      <ts-button type="plain" @click="handleEditDialog"></ts-button>
    </div>
    <div slot="footer" class="classification-footer" v-show="!isExclusive">
      <ts-button :disabled="chooseItem.length<=0" type="primary" v-show="!Params.unbinding" @click="handleUnbindProduct({ids:chooseItem,unbinding:true,classId:Params.classId})"></ts-button>
      <ts-button :disabled="chooseItem.length<=0" v-show="!Params.unbinding" type="primary" @click="handleShowDialog(chooseItem)" v-if="false"></ts-button>
      <ts-pagination class="classification-footer--pagation" @change="handleChangeCurrent" @page-size-change="handleChangePageSize" :current="BindingProductList.pageNO" :total="BindingProductList.totalPage">
      </ts-pagination>
    </div>
    <!--  -->
    <div class="classification-filter">
      <ts-filter title="">
        <ts-radio-group v-model="Params.classId" @change="handleChangeClass">
          <ts-radio :label="item.id" v-for="item in ProductCategory" :key="item.id">{{item.className}}({{item.bindingCount}})</ts-radio>
        </ts-radio-group>
      </ts-filter>
    </div>
    <!--  -->
    <div class="classification-showName" v-show="!isExclusive">
      <ts-switch v-show="!isExclusive" v-model="Params.unbinding" :width="120" onText="" offText=""></ts-switch>
      <span v-show="Pwd.settedPwd&&exclusiveID===Params.classId"><span class="red">{{Pwd.settedPwd}}</span>
      <a @click="handleSetPwd('Pwd','edit')" class="blue"></a></span>
    </div>
    <!--  -->
    <!--  -->
    <ts-menu :prop="BindingProductList.list" v-show="!isExclusive">
      <ts-checkbox-group v-model="chooseItem">
        <ts-menu-table v-for="(item,index) in BindingProductList.list" :key="item.bandId">
          <div slot="header-left">
            <ts-checkbox :label="item.productNo" v-if="!Params.unbinding"></ts-checkbox>
            <span v-else>{{item.productNo}}&nbsp{{item.category | filterDict(dicTree.PRODUCT_SHAPE)}}</span>
          </div>
          <div slot="header-right">
            
            <span v-if="!item.classList.length"></span>
            <span class="classification-table--classList" v-for="i in item.classList">{{i}}</span>
          </div>
          <ts-menu-table-item width="580" class="classification-table--avatar">
            <ts-image width="80" height="80" :src="item.defaultPicUrl"></ts-image>
          </ts-menu-table-item>
          <!-- Price -->
          <ts-menu-table-item>
            <span v-if="item.price>0">{{item.price/100}}/{{item.priceUnit|filterDict(dicTree.PRODUCT_UNIT,'name')}}</span>
            <span v-else></span>
          </ts-menu-table-item>
          <!--  -->
          <ts-menu-table-item>
            <template v-if="Params.unbinding">
            <a class="classification-table--link" @click="handleShowDialog(item.id)"><i class="icon-fangda"></i></a>
            </template>
            <template v-else>
            <a class="classification-table--link" @click="handleUpMoveProductList(item,index)" v-if="index!==0"><i class="icon-yishang"></i></a>
            <a class="classification-table--link" @click="handleDownMoveProductList(item,index)" v-if="index!==BindingProductList.list.length-1"><i class="icon-xiayi"></i></a>
            <a class="classification-table--link" @click="handleUnbindProduct({ids:item.bandId,unbinding:true,classId:Params.classId})"><i class="icon-shanchu_hui shanchu_hui"></i></a>
          </template>
          </ts-menu-table-item>
        </ts-menu-table>
      </ts-checkbox-group>
    </ts-menu>
    <!--  -->
    <div v-show="isExclusive">
      <div class="classification-tip">
        <p class="classification-tip-content">
          <span>6<br></span>
        </p>
      </div>
      <ts-form :model="Pwd" :rules="rules" ref="Pwd" label-width="130px">
        <ts-form-item prop="password" label="">
          <ts-input v-model="Pwd.password" placeholder="" :maxlength="6" width="30%"></ts-input>
          <ts-button type="primary" @click="handleSetPwd('Pwd')"></ts-button>
        </ts-form-item>
      </ts-form>
    </div>
  </ts-section>
  <!-- ========== -->
  <!-- **** -->
  <!-- ========= -->
  <!-- 1. -->
  <ts-dialog v-model="Classification.newDialog" title="" @confirm="handleNew('newClassification')" @cancel="closeNew" width="30%">
    <ts-form :model="Classification" :rules="rules" ref="newClassification">
      <ts-form-item prop="text">
        <ts-input autofocus v-model="Classification.text" placeholder="8" :maxlength="8"></ts-input>
      </ts-form-item>
    </ts-form>
  </ts-dialog>
  <!-- 2. -->
  <ts-dialog v-model="ConfirmDialog.show" width="30%" title="" @cancel="handleCancelBind" @confirm="handleBind" class="classification-dialog">
    <p class="classification-dialog--title">&nbsp;{{Params.classId | filterClassName(ProductCategory)}}&nbsp;</p>
    <p>
      <ts-radio @change.native="handleNoShowDialog" type="origin" v-model="ConfirmDialog.noShowDialog" label="0"><span class="classification-dialog--tip"><i>()</i></span></ts-radio>
    </p>
  </ts-dialog>
  <!-- 3. -->
  <ts-dialog v-model="Classification.editDialog" class="classification-edit-dialog" title="" @cancel="closeEdit" @close="closeEdit" @confirm="handleEdit" :width="getColumnCount*30+'%'">
    <ts-button type="plain" size="large" @click="handleAddNew" v-if="getUserCategoryLength"></ts-button>
    <div class="classification-edit-dialog--column" :style="{'column-count':getColumnCount}">
      <div class="classification-edit-dialog--item onepx-b" v-for="(item,index) in Classification.userCategory">
        <ts-input style="width:230px" :value="item.className" @input="handleInput(item,$event)"></ts-input>
        <i @click="handleUpMoveCategory(item,index)" v-if="index!==0"><i class="icon-yishang"></i></i>
        <i @click="handleDownMoveCategory(item,index)" v-if="index!==Classification.userCategory.length-1"><i class="icon-xiayi"></i></i>
        <i @click="handleDelCategory(item)"><i class="icon-shanchu_hui"></i></i>
      </div>
    </div>
  </ts-dialog>
</div>
</template>

<script>
import {
  deleteProductCategory,
  listSystemProductCategory,
  listUserProductCategory,
  getBindingProductlist,
  addProductCategory,
  updateProductCategory,
  bindingProduct,
  sortProductCategory,
  sortBindingProduct,
  getExclusivePassword,
  updateExclusivePassword
} from '@/common/api/api';
import debounce from 'lodash.debounce';
import {
  mapGetters
} from 'vuex';
export default {
  data() {
    return {
      // 
      Params: {
        classId: null,
        pageNo: 1,
        pageSize: 10,
        unbinding: false
      },
      // cookie
      Cookie: {
        key: 'showInsertDialog',
        value: 1,
        day: 7
      },
      // cookie => 
      ConfirmDialog: {
        noShowDialog: false,
        show: false,
        id: []
      },
      // 
      BindingProductList: {},
      chooseItem: [],
      Classification: {
        // 
        text: '',
        // 
        systemCategory: [],
        // 
        userCategory: [],
        // IDS
        MovedIds: '',
        // 
        editDialog: false,
        // 
        newDialog: false
      },
      Pwd: {
        password: '',
        settedPwd: ''
      },
      isExclusive: false,
      Filter: {
        fabricType: '',
        sort: ''
      },
      rules: {
        password: [{
          required: true,
          message: '',
          trigger: 'blur'
        }, {
          min: 6,
          max: 6,
          message: '6'
        }],
        text: [{
          required: true,
          message: '',
          trigger: 'blur'
        }, {
          min: 1,
          max: 8,
          message: ' 1  8 '
        }]
      }
    };
  },
  beforeDestroy() {
    sessionStorage.setItem('classification-filter', JSON.stringify(this.Params.classId));
  },
  watch: {
    // Params
    Params: {
      async handler(val, oldval) {
        this.BindingProductList = (await getBindingProductlist(val)).data.data;
      },
      deep: true
    }
  },
  filters: {
    filterClassName(val, $arr) {
      try {
        if (!val && val.length === 0) {
          return val;
        }
        let filter = $arr.filter(item => item.id.toString() === val.toString());
        return filter[0].className;
      } catch (e) {
        console.log(e);
      }
    }
  },
  created() {
    !this.cookie.get(this.Cookie.key) ? this.cookie.set(this.Cookie.key, this.Cookie.value, this.Cookie.day, '/') : '';
    this.index().then(() => {
      this.Params.classId = !sessionStorage.getItem('classification-filter') ? this.Classification.systemCategory[0].id : JSON.parse(sessionStorage.getItem('classification-filter'));
    });
  },
  computed: {
    ...mapGetters(['dicTree']),
    // 821
    getColumnCount() {
      return this.Classification.userCategory.length > 8 ? 2 : 1;
    },
    // 
    getUserCategoryLength() {
      return this.Classification.userCategory.length <= 0;
    },
    // 
    ProductCategory() {
      return [...this.Classification.systemCategory, ...this.Classification.userCategory];
    },
    // ID
    exclusiveID() {
      return this.Classification.systemCategory.filter(item => item.className === '')[0].id;
    }
  },
  methods: {
    async index() {
      // 
      this.Classification.systemCategory = (await listSystemProductCategory()).data.data;
      // 
      // XXX:PC 
      this.Classification.userCategory = (await listUserProductCategory({
        pageNo: 1,
        pageSize: 1000
      })).data.data.list;
    },
    async handleChangeClass(e) {
      if (e === this.exclusiveID) {
        let res = (await getExclusivePassword()).data.data;
        this.Pwd.settedPwd = res;
        this.isExclusive = !res;
      } else {
        this.isExclusive = false;
      }
    },
    // 
    handleSetPwd(formName, status = 'create') {
      if (status === 'create') {
        this.$refs[formName].validate(async(valid) => {
          if (valid) {
            this.$nextTick(async() => {
              this.Pwd.settedPwd = await updateExclusivePassword({
                password: this.Pwd.password
              });
            });
          }
        });
      } else {
        this.$nextTick(async() => {
          this.Pwd.settedPwd = (await updateExclusivePassword()).data.data;
          this.$toast('');
        });
      }
    },
    // 
    // =========
    async handleChangeCurrent(current) {
      this.Params.pageNo = current;
    },
    // =>
    handleAddNew() {
      this.closeDialog('editDialog');
      setTimeout(() => {
        this.Classification.newDialog = true;
      }, 400);
    },
    async handleChangePageSize(size) {
      this.Params.pageSize = size;
    },
    // 
    handleUpMoveProductList(item, index) {
      let upMovedArr = this.upMove(this.BindingProductList.list, index).map(item => item.id);
      sortBindingProduct({
        ids: upMovedArr.toString()
      });
    },
    // 
    handleDownMoveProductList(item, index) {
      let downMovedArr = this.downMove(this.BindingProductList.list, index).map(item => item.id);
      sortBindingProduct({
        ids: downMovedArr.toString()
      });
    },
    // 
    handleUpMoveCategory(item, index) {
      let upMovedArr = this.upMove(this.Classification.userCategory, index).map(item => item.id);
      this.Classification.MovedIds = upMovedArr.toString();
    },
    // 
    handleDownMoveCategory(item, index) {
      let downMovedArr = this.downMove(this.Classification.userCategory, index).map(item => item.id);
      this.Classification.MovedIds = downMovedArr.toString();
    },
    async handleDelCategory(item) {
      await deleteProductCategory({
        ids: item.id
      });
      await this.index();
    },
    // 
    async handleEditDialog() {
      this.Classification.editDialog = true;
    },
    // 
    closeDialog(model) {
      this.Classification[model] = false;
    },
    async closeEdit() {
      this.closeDialog('editDialog');
      await this.index();
    },
    // 
    closeNew() {
      this.closeDialog('newDialog');
      this.Classification.text = '';
    },
    /**
    classId	id	number
    id	bandId	number	
    isHot		boolean	truefalse
    productId	id	number	
    unbinding		boolean	true-- false--
     */
    handleUnbindProduct(params) {
      this.$messagebox.confirm('?').then(async action => {
        await bindingProduct({
          classId: params.classId,
          ids: params.ids.toString(),
          unbinding: params.unbinding
        });
        await this.index();
        this.BindingProductList = (await getBindingProductlist(this.Params)).data.data;
      });
    },
    // XXX:=> PC 800
    handleInput: debounce(async function(item, event) {
      await updateProductCategory({
        className: event,
        id: item.id
      });
    }, 500),
    // 
    handleNew(formName) {
      this.$refs[formName].validate(async(valid) => {
        if (valid) {
          await addProductCategory({
            className: this.Classification.text
          });
          await this.index();
          await this.closeDialog('newDialog');
          this.Classification.text = '';
        }
      });
    },
    async handleEdit(formName) {
      if (this.Classification.MovedIds) {
        await sortProductCategory({
          ids: this.Classification.MovedIds
        });
        await this.index();
      }
      this.closeDialog('editDialog');
    },
    // =>cookie
    handleShowDialog(item) {
      this.ConfirmDialog.id = item;
      if (this.cookie.get(this.Cookie.key) === '1') {
        this.ConfirmDialog.show = true;
      } else {
        this.handleBind();
      }
    },
    // 
    handleCancelBind() {
      this.ConfirmDialog.show = false;
      this.cookie.set(this.Cookie.key, this.Cookie.value, this.Cookie.day, '/');
    },
    // 
    async handleBind() {
      await bindingProduct({
        classId: this.Params.classId,
        isHot: this.Params.classId === this.ProductCategory.filter(item => item.className === '')[0].id,
        productId: this.ConfirmDialog.id.toString()
      });
      await this.index();
      this.ConfirmDialog.show = false;
      this.chooseItem = [];
      this.BindingProductList = (await getBindingProductlist(this.Params)).data.data;
    },
    // cookie
    handleNoShowDialog(e) {
      this.cookie.set(this.Cookie.key, e, this.Cookie.day, '/');
    }
  }
};
</script>

<style lang="css" scoped>
.red{
  color: red;
}
.blue{
  color: #4c93fd;
}
@component-namespace classification{
  @component showName{
    margin: 15px 0 20px 0;
    color:#666666;
  }
  @component table{
    @modifier classList{
      margin-right: 6px;
      position: relative;
      &:last-child{
        position:static;
        margin-right: 0;
        &:after{
          content:'';
        }
      }
      &:after{
        /*position: absolute;*/
        content: "|";
        margin-left: 4px;
        transform: translate(-50%,0);
      }
    }
  }
  @component footer{
    display: flex;
    @modifier pagation{
      flex:1;
      text-align: right;
    }
  }
  @component dialog{
    p{
      text-align: center;
    }
    @modifier title{
      font-size: 18px;
      font-weight: 500;
      margin-bottom: 10px;
    }
    @modifier tip{
      font-size: 14px;
      i{
        font-size: smaller;
        color: #3F3F3F;
      }
    }
  }
  @component tip{
    background: #FFF7E3;
    padding: 10px;
    border: 1px solid #f2d794;
    border-radius: 4px;
    margin: 10px 0;
  }
  @component edit-dialog{
    @modifier column{
      column-gap: 10px;
    }
    @modifier item{
      line-height: 54px;
      i{
        margin-left:8px;
        cursor: pointer;
        transition: .5s;
        &:hover{
          color:#4C93FD;
        }
      }
    }
  }
}
.icon-shanchu_hui{
  &:before{
    color:#666;
  }
}
</style>



